<template>
  <div>
    <dashboard-table
      class="mt-8"
      v-if="items.length > 0"
      :headers="headers"
      :items="items"
      :loading="loading"
      :noPagination="true"
      title="Solicitações"
      @approve="approve"
      @reprove="reprove"
    />
    <overlay ref="overlay" />
  </div>
</template>

<script>
import Axios from '@/plugins/axios'
import BarberMixin from '@/mixins/BarberMixin'
import DashboardTable from '@/components/admin/dashboard/DashboardTable'
import Overlay from '@/components/globais/overlay/Overlay'
import OverlayMixin from '@/mixins/OverlayMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
export default {
  name: 'BarberRequests',
  components: { DashboardTable, Overlay },
  mixins: [ BarberMixin, OverlayMixin, SnackbarMixin ],
  data: () => ({
    headers: [
      { text: '', value: 'image', class: 'text-center', type: 'image' },
      { text: 'Barbeiro', value: 'name', class: 'text-left' },
      { text: 'E-mail', value: 'email', class: 'text-left' },
      { text: 'Tipo', value: 'status', class: 'text-center' },
      { text: '#', value: 'actions', type: 'action', class: 'text-center' }
    ],
    items: [],
    loading: true
  }),
  mounted () {
    this.getBarbers()
  },
  methods: {
    approve (barber) {
      this.$refs.overlay.showOverlay('Aprovando...')
      Axios.post(`/barbershop-request/${barber.id}/approve`)
      .then(response => {
        if (response.data.codigo === 200) {
          this.$refs.overlay.hideOverlaySuccess('Barbeiro aprovado!')
          let index = this.items.findIndex(i => i.id === barber.id)
          this.items.splice(index, 1)
        } else this.setError(response.data.corpo)
      }).catch (() => this.setError('Não foi possível aprovar a solicitação!'))
    },
    reprove (barber) {
      this.$refs.overlay.showOverlay('Reprovando...')
      Axios.delete(`/barbershop-request/${barber.id}/reprove`)
      .then(response => {
        if (response.data.codigo === 200) {
          this.$refs.overlay.hideOverlaySuccess('Solicitação reprovada!')
          let index = this.items.findIndex(i => i.id === barber.id)
          this.items.splice(index, 1)
        } else this.setError(response.data.corpo)
      }).catch (() => this.setError('Não foi possível reprovar a solicitação!'))
    },
    getBarbers () {
      Axios.get(`/barbershop-request/barbershop/${this.barber.barbershop_id}`)
      .then(response => {
        if (response.data.codigo === 200) {
          this.items = response.data.corpo.map(item => {
            item.actions = [
              { icon: 'check', text: 'Aprovar', class: 'success text-none', action: 'approve' },
              { icon: 'remove', text: 'Reprovar', class: 'danger text-none', action: 'reprove' }
            ]
            item.image = item.barber.profile
            item.name = item.barber.name
            item.email = item.barber.email
            item.status = 'Aguardando aprovação'
            return item
          })
        }
        this.loading = false
      }).catch(() => this.loading = false)
    },
    getUrl (image) {
      return `${this.$url_backend}${image}`
    },
    setError (msg) {
      this.$refs.overlay.hideOverlay()
      this.showSnackbarErro(msg)
    }
  }
}
</script>

<style lang="scss" scoped>
@import 'src/scss/theme.scss';
.barbers { width: 100%; overflow: hidden; overflow-x: auto;
  table { width: 100%; min-width: 600px; border-spacing: 0;
    thead tr th { border-bottom: 2px solid $primaryLight; padding: 0 10px; }
    tbody tr { border-bottom: 1px solid $muted;
      td { padding: 10px; margin: 0 15px; }
    }
    td, th {
      &:first-child { width: 100px; margin: 0; } 
      .image {
        overflow: hidden;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        display: block;
        background-color: #cecece;
      }
      &.status { min-width: 100px; }
    }
  }
}
</style>