<template>
  <div class="barber container pa-0">
    <v-row class="mt-4">
      <v-col cols="auto">
        <btn-secondary
          text="Adicionar"
          icon="add"
          @clicked="addBarber"
        />
      </v-col>
      <v-spacer></v-spacer>
    </v-row>
    <barber-requests />
    <dashboard-table
      class="mt-8"
      :headers="headers"
      :items="items"
      :loading="loading"
      :noPagination="true"
      title="Barbeiros"
      @blockBarber="blockBarber"
      @unblockBarber="unblockBarber"
    />
    <register-barber-dialog ref="registerBarberDialog" />
    <overlay ref="overlay" />
  </div>
</template>

<script>
import Axios from '@/plugins/axios'
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'
import DashboardTable from '@/components/admin/dashboard/DashboardTable'
import Overlay from '@/components/globais/overlay/Overlay'
import RegisterBarberDialog from '@/components/admin/barber/RegisterBarberDialog'
import BarberRequests from '@/components/admin/barber/BarberRequests'
import BarberMixin from '@/mixins/BarberMixin'
import OverlayMixin from '@/mixins/OverlayMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import BarberStatusMixin from '@/mixins/BarberStatusMixin'
export default {
  name: 'Barber',
  components: { BtnSecondary, DashboardTable, Overlay, RegisterBarberDialog, BarberRequests },
  mixins: [ BarberMixin, OverlayMixin, SnackbarMixin, BarberStatusMixin ],
  data: () => ({
    dialogDescription: null,
    headers: [
      { text: '', value: 'image', type: 'image', class: 'text-center' },
      { text: 'Barbeiro', value: 'name', class: 'text-left' },
      { text: 'Tipo', value: 'status', class: 'text-center' },
      { text: '#', value: 'actions', type: 'action', class: 'text-center' }
    ],
    items: [],
    loading: false
  }),
  mounted () {
    this.getBarbers()
  },
  methods: {
    addBarber () {
      this.$refs.registerBarberDialog.showDialog()
    },
    blockBarber (item) {
      const barberId = item.id
      this.$refs.overlay.showOverlay('Aguardando...')
      Axios.post(`/barber/block/${barberId}`)
      .then(response => {
        if (Number(response.data.codigo) === 200) {
           const index = this.items.findIndex(i => Number(i.id) === Number(barberId))
          if (index > -1) {
            this.items[index].status = this.getBarberStatusTextByStatusId(this.$BARBER_STATUS.BLOQUEADO)
            this.items[index].actions = [{ icon: 'lock_open', text: 'Desbloquear', class: 'gray2 text-none', action: 'unblockBarber' }]
          }
          this.$refs.overlay.hideOverlaySuccess('Barbeiro bloqueado!')
        } else this.setError(response.data.corpo)
      }).catch (() => this.setError('Não foi possível bloquear o barbeiro!'))
    },
    getBarbers () {
      Axios.get(`/barber/by-barbershop`)
      .then(response => {
        if (response.data.codigo === 200) {
          this.items = response.data.corpo.map(item => {
            let actions = []
            if (Number(item.status.id) === (this.$BARBER_STATUS.ATIVO)) {
              if (Number(this.barber.id) !== Number(item.id)) {
                actions.push({ icon: 'lock', text: 'Bloquear', class: 'success text-none', action: 'blockBarber' })
              }
            } else {
              actions.push({ icon: 'lock_open', text: 'Desbloquear', class: 'gray2 text-none', action: 'unblockBarber' })
            }
            item.image = item.image_url
            item.actions = actions
            item.status = item.status.name
            return item
          })
        } else this.setError(response.data.corpo)
      }).catch (() => this.setError('Não foi possível cadastrar a barbearia!'))
    },
    setError (msg) {
      this.$refs.overlay.hideOverlay()
      this.showSnackbarErro(msg)
    },
    unblockBarber (item) {
      const barberId = item.id
      this.$refs.overlay.showOverlay('Aguardando...')
      Axios.post(`/barber/unlock/${barberId}`)
      .then(response => {
        if (Number(response.data.codigo) === 200) {
          const index = this.items.findIndex(i => Number(i.id) === Number(barberId))
          if (index > -1) {
            this.items[index].status = this.getBarberStatusTextByStatusId(this.$BARBER_STATUS.BLOQUEADO)
            this.items[index].actions = [{ icon: 'lock', text: 'Bloquear', class: 'success text-none', action: 'blockBarber' }]
          }
          this.$refs.overlay.hideOverlaySuccess('Barbeiro desbloqueado!')
        } else this.setError(response.data.corpo)
      }).catch (() => this.setError('Não foi possível desbloquear o barbeiro!'))
    }
  }
}
</script>